import { request, requestDownload, getHeaderAuthorization } from 'utils';

const prefix = '/api/organization';

export const getStaffProfiles = ({
  staffId,
  fullname,
  gender,
  educationType,
  title,
  socialBookNo,
  idCard,
  contractTypeId,
  positionId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/StaffProfile',
    method: 'GET',
    params: { staffId, fullname, gender, educationType, title, socialBookNo, idCard, contractTypeId, positionId, page,pageSize, orderBy, isPagingEnabled },
    data: {}
  });


  export const createStaffProfile = data =>
    request({
      prefix,
      headers: getHeaderAuthorization(),
      url: '/StaffProfile',
      method: 'POST',
      params: {},
      data
    });

export const updateStaffProfile = ({id,staffId,firstname,lastname,dob,gender,ethnicityId,recruitedDate,townId,politicalYear,sfcId,facultyId,subjectGroupId,unitId,unit,educationLevel,educationType,title,afterUniversity,universityBachelor,collegeBachelor,politicalTheory,informationTechnology,foreignLanguage,pedagogicTrain,hasOtherCert,hasOtherCert2,rcId,positionId,assignedDate,currentLevel,levelStartDate,note,contractTypeId,contractExpiredDate,resignedDate,resignAnnouncedDate,socialBookNo,idCard,updatedById} = {}) =>
  request({
    prefix,
    headers: getHeaderAuthorization(),
    url: `/StaffProfile/${id}`,
    method: 'PUT',
    params: {},
    data: {id,staffId,firstname,lastname,dob,gender,ethnicityId,recruitedDate,townId,politicalYear,sfcId,facultyId,subjectGroupId,unitId,unit,educationLevel,educationType,title,afterUniversity,universityBachelor,collegeBachelor,politicalTheory,informationTechnology,foreignLanguage,pedagogicTrain,hasOtherCert,hasOtherCert2,rcId,positionId,assignedDate,currentLevel,levelStartDate,note,contractTypeId,contractExpiredDate,resignedDate,resignAnnouncedDate,socialBookNo,idCard,updatedById}
  });

export const deleteStaffProfile = id =>
  request({
    prefix,
    headers: getHeaderAuthorization(),
    url: `/StaffProfile/${id}/Delete`,
    method: 'PUT',
  });

export const getStaffProfile = ({id} = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });

export const getStaffProfileUnits = ({label,page,pageSize=10,orderBy,isPagingEnabled=true} = {}) =>
  request({
    prefix,
    url: '/StaffProfile/Units',
    method: 'GET',
    params: {label,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });
  
export const getStaffLevelHistories = ({id} = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/LevelUpHistory`,
    method: 'GET',
    params: {},
    data: {}
  });

export const getStaffDocuments = ({id} = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/StaffDocuments`,
    method: 'GET',
    params: {},
    data: {}
  });

export const deleteStaffDocument = id =>
  request({
    prefix,
    headers: getHeaderAuthorization(),
    url: `/StaffProfile/StaffDocuments/${id}/Delete`,
    method: 'DELETE',
    params: {},
    data: {}
  });

export const uploadStaffDocument = ({ id, formData }) =>
  request({
    prefix,
    headers: getHeaderAuthorization(),
    url: `/StaffProfile/${id}/UploadDocument`,
    method: 'POST',
    params: {},
    data: formData
  });


export const updateStaffDocument = ({id, documentId, formData} = {}) =>
  request({
  prefix,
  headers: getHeaderAuthorization(),
  url: `/StaffProfile/${id}/UpdateDocument/${documentId}`,
  method: 'PUT',
  params: {},
  data: formData
  });


export const exportOriginalSalaryIncrease = () =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportOriginalSalaryIncrease.xlsx',
    url: '/StaffProfile/ExportOriginalSalaryIncrease',
    data: {},
    headers: getHeaderAuthorization(),
    method: 'POST',
  });

