import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnEdit, BtnDelete, BtnExport } from 'components/common/button';
import { CartHeadAction, TableData } from 'components/common';
// import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message, Popconfirm, Spin } from 'antd';
import {
  getStaffProfiles,
  getStaffProfile,
  deleteStaffProfile,
  exportOriginalSalaryIncrease,
  exportRaiseLevelStaff,
  exportBeyondSeniority,
  exportStaffProfileExcel
} from 'services/staffProfile';
import FormSearchStaffProfile from './FormSearchStaffProfile';
import StaffProfilePopup from './StaffProfilePopup';

class StaffProfile extends Component {
  state = {
    paramSearch: {},
    reload: 0,
    profileData: {},
    isShowPopup: false,
    loading: false
  };

  onAddNew = () => {
    this.setState({
      isShowPopup: true,
      profileData: {}
    })
  }

  onEdit = async id => {
    this.setState({ loading: true });
    try {
      const result = await getStaffProfile({ id });
      if(result.data) {
        this.setState({
          profileData: result.data,
          isShowPopup: true,
        });
      }
    } catch(error) {
      message.error('C li xy ra');
    }
    this.setState({ loading: false });
  }

  onRemove = async id => {
    try {
      await deleteStaffProfile(id);
      message.success('Xa thnh cng');
      this.setState({
        reload: this.state.reload + 1,
      });
    } catch(error) {
      message.error('C li xy ra');
    }
  }

  dataFormSeach = data => {
    this.setState({
      paramSearch: data,
      reload: this.state.reload + 1,
    });
  };

  handleExportSalaryIncrease = async () => {
    this.setState({ loading: true });
    await exportOriginalSalaryIncrease();
    this.setState({ loading: false });
  }

  handleExportRaiseLevelStaff = async () => {
    this.setState({ loading: true });
    await exportRaiseLevelStaff();
    this.setState({ loading: false });
  }

  handleBeyondSeniority = async () => {
    this.setState({ loading: true });
    await exportBeyondSeniority();
    this.setState({ loading: false });
  }

  handleExportStaffProfile = async () => {
    this.setState({ loading: true });
    await exportStaffProfileExcel();
    this.setState({ loading: false });
  }

  render() {
    const { isShowPopup, profileData } = this.state;
    const columns = [
      {
        title: 'M',
        dataIndex: 'staffId',
        key: 'staffId',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
        align: 'center'
      },
      {
        title: 'Gii tnh',
        dataIndex: 'genderText',
        key: 'genderText',
        align: 'center'
      },
      {
        title: 'Ngy tuyn dng',
        dataIndex: 'recruitedDate',
        key: 'recruitedDate',
        align: 'center'
      },
      {
        title: 'Bc o to',
        dataIndex: 'educationLevel',
        key: 'educationLevel',
        align: 'center'
      },
      {
        title: 'Danh hiu cao nht',
        dataIndex: 'title',
        key: 'title',
      },
      {
        title: 'Chc v',
        dataIndex: 'position',
        key: 'position',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: (text, record) => (
          <Fragment>
            <BtnEdit onClick={() => this.onEdit(record.id)} />
            <Popconfirm
              title="Bn mun xa?"
              okText="Xa"
              cancelText="Hy"
              onConfirm={() => this.onRemove(record.id)}
            >
              <BtnDelete />
            </Popconfirm>
          </Fragment>
        ),
      },
    ];
    return (
      <Fragment>
        <StaffProfilePopup
          profile={profileData}
          visible={isShowPopup}
          onClose={() => this.setState({ isShowPopup: false })}
          reloadProfile={() => this.setState({reload: this.state.reload + 1})}
        />
        <Spin spinning={this.state.loading}>
          <Card title="Danh mc chc v">
            <CartHeadAction>
              <BtnAddNew onClick={this.onAddNew} />
              <BtnExport className="btnExport" title="Xut danh sch nng lng gc" onClick={this.handleExportSalaryIncrease} />
              <BtnExport className="btnExport" title="Xut thm nin" onClick={this.handleExportRaiseLevelStaff} />
              <BtnExport className="btnExport" title="Xut vt khung" onClick={this.handleBeyondSeniority} />
              <BtnExport className="btnExport" title="Xut danh sch cn b theo n v" onClick={this.handleExportStaffProfile} />
            </CartHeadAction>
            <Row>
              <Col span={24}>
                <FormSearchStaffProfile dataForm={this.dataFormSeach} />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <TableData
                  paramSearch={this.state.paramSearch}
                  formSearch="true"
                  reload={this.state.reload}
                  service={getStaffProfiles}
                  columns={columns}
                  rowKey="id"
                />
              </Col>
            </Row>
          </Card>
        </Spin>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <StaffProfile />
  </Page>
);



