import React, { Component } from 'react';
// import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Modal, message, Spin } from 'antd';
import { createStaffProfile, updateStaffProfile } from 'services/staffProfile';
import Profile from './Profile'

class StaffProfilePopup extends Component {

  state = {
    loading: false
  }

  onClose = () => {
    this.props.onClose();
    this.formRef.props.form.resetFields();
  }
  
  saveFormRef = formRef => {
    this.formRef = formRef;
  };

  handleCreate = async () => {
    const { form, profile: { id } } = this.formRef.props;
    const { avatarFile } = this.formRef.state;
    form.validateFields(async(err, values) => {
      this.setState({ loading: true });
      if (err) {
        return;
      }
      try {
        let result = {};
        if(id) {
          result = await updateStaffProfile({...values, id})
        } else {
          const formData = new FormData();
          Object.entries(values).forEach(([key, value]) => {
            formData.append(key, value);
          })
          formData.append("formFile", avatarFile);
          result = await createStaffProfile(formData);
        }
        if(result.status === 200) {
          this.onClose()
          message.success(id ? 'Cp nht thnh cng!' : 'Thm thnh cng!');
        } else {
          message.error('C li xy ra');
        }
        form.resetFields();
        this.props.reloadProfile();
        this.setState({ loading: false });
      } catch (errorrr) {
        message.error('C li xy ra');
        this.setState({ loading: false });
      }
    });
  };

  render() {
    return (
      <Modal
        title=""
        visible={this.props.visible}
        onOk={this.handleCreate}
        okText="Lu"
        onCancel={this.onClose}
        cancelText="Hy"
        width={1200}
      >
        <Spin spinning={this.state.loading}>
          <Profile profile={this.props.profile || {}} isPopupClose={this.props.visible} wrappedComponentRef={this.saveFormRef} />
        </Spin>
      </Modal>
    )
  }
}

export default StaffProfilePopup;



