import { request } from 'utils';

const prefix = '/api/filestorage';

export const getFile = (id) =>
  request({
    prefix,
    url: `/Storage/${id}/Download`,
    method: 'GET',
    params: {},
    data: {}
  });


export const getFileInfo = (id) =>
  request({
    prefix,
    url: `/Storage/${id}/Info`,
    method: 'GET',
    params: {},
    data: {}
  });



