import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common/index';
// import { getContractTypes } from 'service/contractType';
import { getContractTypes} from 'services/contractType';
import { getStaffCategories } from 'services/staffCategory';
// import './style.scss';

const { Option } = Select;

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStaffProfile extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { form: { getFieldDecorator } } = this.props;
    return (
      <Form className="form-inline-label search-form" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="H v tn">
              {getFieldDecorator('fullname', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="M">
              {getFieldDecorator('staffId', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Bc o to">
              {getFieldDecorator('educationLevel', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loi hnh o to">
              {getFieldDecorator('educationType', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Danh hiu cao nht">
              {getFieldDecorator('title', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="S bo him">
              {getFieldDecorator('SocialBookNo', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="CMND">
              {getFieldDecorator('idCard', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Gii tnh">
              {getFieldDecorator('gender', {})(
                <Select>
                  <Option value="" />
                  <Option value>Nam</Option>
                  <Option value={false}>N</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loi hp ng">
              {getFieldDecorator('contractTypeId', {})(<SelectAjax service={getContractTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Chc v">
              {getFieldDecorator('positionId', {})(<SelectAjax service={getStaffCategories} />)}
            </Form.Item>
          </Col>
          <Col span={6}>  
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStaffProfile;



