import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getRankConfigs = paramsSearch =>
  request({
    prefix,
    url: `/RankConfig`,
    params: { ...paramsSearch },
    method: 'GET',
  });

export const getUser = () =>
  request({
    prefix: '/identity',
    url: `/sign-in`,
    data: { username: 'admin', password: 'admin' },
    method: 'POST',
  });

export const createRankConfig = (newDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/RankConfig`,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    data: { ...newDoc },
    method: 'POST',
  });

export const deleteRankConfig = (id, token, headers = {}) =>
  request({
    prefix,
    url: `/RankConfig`,
    params: { id },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'DELETE',
  });

export const getRankConfigId = id =>
  request({
    prefix,
    url: `/RankConfig/GetById`,
    params: { id },
    method: 'GET',
  });

export const updateRankConfig = (editDoc, token, headers = {}) =>
  request({
    prefix,
    url: `/RankConfig`,
    data: { ...editDoc },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'PUT',
  });

export const downloadFile = (paramsSearch, token, headers = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportRankConfigs.xlsx',
    url: `/RankConfig/ExportRankConfigs`,
    data: { ...paramsSearch },
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`,
      ...headers,
    },
    method: 'POST',
  });



